.widget {
  background: $light-blue;
}

.widget__message {
  color: $off-white;
  border-bottom: 1px solid lighten($light-blue, 10%);
  margin: 0;
  color: $off-white;
  padding: $l-size;
  text-align: center;
}

.widget-header {
  background: $blue;
  color: $off-white;
  padding: $m-size;
  display: flex;
  justify-content: space-between;
}

.widget-header__title {
  margin: 0;
}


